:root {
    --primary: #db1a20;
    --secondary: #aab1ae;
    --tertiary: #a30e00;
    --light: #fafafa;
    --black: #333333;
    --error: rgb(228, 46, 1);
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
}

* {
    box-sizing: border-box;
}

html,
#root,
body {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    font-family: "Karla", sans-serif;
    font-weight: 400;
    line-height: 1.5;
    font-size: 20px;
    color: var(--light);
}

#root {
    display: flex;
    flex-direction: column;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    margin: 5px 0;
    line-height: 1.25;
}

h1 {
    font-size: 2.25rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.75rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.25rem;
}

h6 {
    font-size: 1rem;
}

p {
    font-size: 1.1rem;
    margin-top: 0;
}

a {
    color: var(--tertiary);
    text-decoration: none;
    font-weight: bold;
}

a:hover {
    text-decoration: underline;
}

header {
    background: var(--primary);
}

header a {
    color: var(--light);
}

button {
    font-size: 1rem;
    text-decoration: none;
    padding: 0.25rem 1rem;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    cursor: pointer;
    margin: 0 1px;
    background-color: var(--tertiary);
}

button:hover {
    opacity: 0.8;
}

button[disabled] {
    cursor: default;
    opacity: 0.8;
}

img {
    max-width: 100%;
}

span[role="img"] {
    cursor: default;
}

form {
    width: 40%;
}

input {
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid var(--black);
    outline: none;
    border-radius: 3px;
    transition: box-shadow 0.2s;
}

input:focus {
    border: 1px solid var(--tertiary);
    box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

.ai-c {
    align-items: center;
}

.c-primary {
    color: #db1a20;
}

.carousel-link {
    padding-bottom: 1rem;
}

.container {
    flex-grow: 1;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.container-card {
    align-content: center;
    flex-grow: 1;
    max-width: 1200px;
    margin-left: auto;
    margin-right: auto;
}

.container-center {
    text-align: center;
}

/* .card-container {
  flex-grow: 1;
  max-width: 1200px;
  margin-left: auto;
  margin-right: auto;
} */

.d-b {
    display: block;
}

.d-f {
    display: flex;
    flex-wrap: nowrap;
}

.flex-row {
    display: flex;
    flex-wrap: wrap;
}

.flex-end {
    justify-content: flex-end;
}

.img-avatar {
    height: 20rem;
    border-radius: 50%;
}

.jc-c {
    justify-content: center;
}

.mb-1 {
    margin-bottom: var(--spacing-one);
}

.mx-1 {
    margin-right: var(--spacing-one);
    margin-left: var(--spacing-one);
}

.mx-2 {
    margin-right: var(--spacing-two);
    margin-left: var(--spacing-two);
}

.my-1 {
    margin-top: var(--spacing-one);
    margin-bottom: var(--spacing-one);
}

.my-2 {
    margin-top: var(--spacing-two);
    margin-bottom: var(--spacing-two);
}

.px-1 {
    padding-right: var(--spacing-one);
    padding-left: var(--spacing-one);
}

.px-2 {
    padding-right: var(--spacing-two);
    padding-left: var(--spacing-two);
}

.py-1 {
    padding-top: var(--spacing-one);
    padding-bottom: var(--spacing-one);
}

.py-2 {
    padding-top: var(--spacing-two);
    padding-bottom: var(--spacing-two);
}

.space-between {
    justify-content: space-between;
}

.ta-l {
    text-align: left;
}

.ws-nw {
    white-space: nowrap;
}

.card {
    text-align: center;
}

.card-margin {
    margin-left: auto;
    margin-right: auto;
    margin-top: 2rem;
}

.error-text {
    color: var(--error);
}

@media screen and (max-width: 768px) {
    .card {
        width: 100%;
    }
}
