@import url(https://fonts.googleapis.com/css2?family=Shadows+Into+Light&display=swap);
:root {
    --primary: #db1a20;
    --secondary: #aab1ae;
    --tertiary: #a30e00;
    --light: #fafafa;
    --black: #333333;
    --error: rgb(228, 46, 1);
    --spacing-one: calc(1rem * 0.5);
    --spacing-two: calc(1rem);
}

* {
    box-sizing: border-box;
}

html,
#root,
body {
    height: 100%;
}

body {
    margin: 0;
    padding: 0;
    min-height: 100vh;
    font-family: "Karla", sans-serif;
    font-weight: 400;
    line-height: 1.5;
    font-size: 20px;
    color: #fafafa;
    color: var(--light);
}

#root {
    display: flex;
    flex-direction: column;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-family: "Rubik", sans-serif;
    font-weight: 500;
    margin: 5px 0;
    line-height: 1.25;
}

h1 {
    font-size: 2.25rem;
}

h2 {
    font-size: 2rem;
}

h3 {
    font-size: 1.75rem;
}

h4 {
    font-size: 1.5rem;
}

h5 {
    font-size: 1.25rem;
}

h6 {
    font-size: 1rem;
}

p {
    font-size: 1.1rem;
    margin-top: 0;
}

a {
    color: #a30e00;
    color: var(--tertiary);
    text-decoration: none;
    font-weight: bold;
}

a:hover {
    text-decoration: underline;
}

header {
    background: #db1a20;
    background: var(--primary);
}

header a {
    color: #fafafa;
    color: var(--light);
}

button {
    font-size: 1rem;
    text-decoration: none;
    padding: 0.25rem 1rem;
    border-radius: 0.5rem;
    border: none;
    outline: none;
    cursor: pointer;
    margin: 0 1px;
    background-color: #a30e00;
    background-color: var(--tertiary);
}

button:hover {
    opacity: 0.8;
}

button[disabled] {
    cursor: default;
    opacity: 0.8;
}

img {
    max-width: 100%;
}

span[role="img"] {
    cursor: default;
}

form {
    width: 40%;
}

input {
    font-size: 1rem;
    padding: 0.25rem;
    border: 1px solid #333333;
    border: 1px solid var(--black);
    outline: none;
    border-radius: 3px;
    transition: box-shadow 0.2s;
}

input:focus {
    border: 1px solid #a30e00;
    border: 1px solid var(--tertiary);
    box-shadow: 0 0 0.25rem rgba(0, 0, 0, 0.5);
}

